* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding: 0;
  margin: 0;
}

.item{
  list-style: none;

  display: flex;
  align-items: center;
  gap: 15px;
}

.item-img{
  width: 70px;
  height: 55px;
}

.container-header{
  display: flex;
  align-items: center;
  gap: 15px;
}

.container{
  margin-bottom: 15px;
}

.container:last-child{
  margin-bottom: 0px;
}

.container-text{
  font-size: 20px;
  font-weight: bold;
}